/* eslint-disable react-hooks/exhaustive-deps */
"use client"

import { useState, useEffect } from "react"
import { useSearchParams } from "next/navigation"
import { Card, CardHeader, CardContent, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { FileUp, Loader } from "lucide-react"
import { Alert, AlertDescription } from "@/components/ui/alert"

// Define the interface for document objects
interface SubmittedDocument {
  idPFE: number;
  idLivrable: 1 | 2; // Limit to the values we know exist
  fichier: string;
  // Add other properties if they exist
}

// Define the interface for livrable names mapping
interface LivrableNames {
  [key: number]: string;
}

export default function SoumettrePage() {
  const searchParams = useSearchParams()
  const idEtudiant = searchParams.get("idEtudiant") || "4" // Default for testing
  const idGroupe = searchParams.get("idGroupe") || "2" // Get from URL or default

  // State for form files
  const [pdfFile, setPdfFile] = useState<File | null>(null)
  const [presentationFile, setPresentationFile] = useState<File | null>(null)
  
  // Form submission state
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [submitSuccess, setSubmitSuccess] = useState(false)
  const [submitError, setSubmitError] = useState<string | null>(null)

  // State for submitted documents with proper typing
  const [submittedDocuments, setSubmittedDocuments] = useState<SubmittedDocument[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [fetchError, setFetchError] = useState<string | null>(null)

  // Map livrable IDs to human-readable names with proper typing
  const livrableNames: LivrableNames = {
    1: "Document PDF",
    2: "Présentation",
  }

  // Fetch submitted documents function
  const fetchDocuments = async () => {
    setIsLoading(true)
    setFetchError(null)
    try {
      const response = await fetch(
        `http://localhost:5000/api/livrable/documents?idEtudiant=${idEtudiant}`
      )
      if (response.ok) {
        const data = await response.json() as SubmittedDocument[]
        setSubmittedDocuments(data)
      } else {
        setFetchError("Failed to fetch documents")
      }
    } catch (error) {
      setFetchError("Error fetching documents")
      console.error("Fetch error:", error)
    } finally {
      setIsLoading(false)
    }
  }

  // Fetch submitted documents when component mounts
  useEffect(() => {
    fetchDocuments()
  }, [idEtudiant])

  // Handle file selection
  const handlePdfChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files?.[0]) setPdfFile(e.target.files[0])
  }

  const handlePresentationChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files?.[0]) setPresentationFile(e.target.files[0])
  }

  // Reset form
  const resetForm = () => {
    setPdfFile(null)
    setPresentationFile(null)
    // Reset file input elements
    const pdfInput = document.getElementById('pdf') as HTMLInputElement
    const presentationInput = document.getElementById('presentation') as HTMLInputElement
    if (pdfInput) pdfInput.value = ''
    if (presentationInput) presentationInput.value = ''
  }

  // Handle form submission
  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault()
    setSubmitSuccess(false)
    setSubmitError(null)
    setIsSubmitting(true)

    if (!pdfFile || !presentationFile) {
      setSubmitError("Veuillez sélectionner les deux fichiers.")
      setIsSubmitting(false)
      return
    }

    const formData = new FormData()
    formData.append("files", pdfFile)
    formData.append("files", presentationFile)
    formData.append("idEtudiant", idEtudiant)
    formData.append("idGroupe", idGroupe)
    formData.append("livrables", JSON.stringify([1, 2])) // 1 for PDF, 2 for presentation

    try {
      const response = await fetch("http://localhost:5000/api/livrable/submit-documents", {
        method: "POST",
        body: formData,
      })

      if (response.ok) {
        setSubmitSuccess(true)
        resetForm()
        fetchDocuments() // Refresh document list after submission
      } else {
        const errorText = await response.text()
        setSubmitError(`Erreur lors de la soumission: ${errorText}`)
      }
    } catch (error) {
      console.error("Erreur de connexion:", error)
      setSubmitError("Erreur de connexion au serveur")
    } finally {
      setIsSubmitting(false)
    }
  }

  return (
    <Card className="w-full max-w-2xl mx-auto shadow-md">
      <CardHeader>
        <CardTitle>Soumettre des documents</CardTitle>
      </CardHeader>
      <CardContent>
        {/* Feedback alerts */}
        {submitSuccess && (
          <Alert className="mb-4 bg-green-50 text-green-800 border-green-200">
            <AlertDescription>
              Documents soumis avec succès!
            </AlertDescription>
          </Alert>
        )}
        
        {submitError && (
          <Alert className="mb-4 bg-red-50 text-red-800 border-red-200">
            <AlertDescription>
              {submitError}
            </AlertDescription>
          </Alert>
        )}

        {/* Submitted Documents Section */}
        <div className="mb-6">
          <h2 className="text-lg font-semibold mb-2">Documents Soumis</h2>
          {isLoading ? (
            <div className="flex items-center space-x-2">
              <Loader className="h-4 w-4 animate-spin" />
              <p>Chargement des documents...</p>
            </div>
          ) : fetchError ? (
            <p className="text-red-500">{fetchError}</p>
          ) : submittedDocuments.length > 0 ? (
            <ul className="list-disc pl-5 space-y-1">
              {submittedDocuments.map((doc) => (
                <li key={`${doc.idPFE}-${doc.idLivrable}`}>
                  {/* Now TypeScript knows that doc.idLivrable is safe to use as an index */}
                  {livrableNames[doc.idLivrable]}:{" "}
                  <a
                    href={`http://localhost:5000${doc.fichier}`}
                    download
                    className="text-blue-500 underline"
                  >
                    Télécharger
                  </a>
                </li>
              ))}
            </ul>
          ) : (
            <p>Aucun document soumis pour le moment.</p>
          )}
        </div>

        {/* Submission Form */}
        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label htmlFor="pdf" className="block text-sm font-medium text-gray-700">
              Document PDF
            </label>
            <input
              type="file"
              id="pdf"
              accept=".pdf"
              onChange={handlePdfChange}
              className="mt-1 block w-full"
              required
            />
            {!pdfFile && submitError && (
              <p className="text-sm text-red-500 mt-1">Veuillez sélectionner un fichier PDF</p>
            )}
          </div>
          <div>
            <label
              htmlFor="presentation"
              className="block text-sm font-medium text-gray-700"
            >
              Présentation
            </label>
            <input
              type="file"
              id="presentation"
              accept=".ppt,.pptx,.key,.pdf"
              onChange={handlePresentationChange}
              className="mt-1 block w-full"
              required
            />
            {!presentationFile && submitError && (
              <p className="text-sm text-red-500 mt-1">Veuillez sélectionner un fichier de présentation</p>
            )}
          </div>
          <Button 
            type="submit" 
            className="w-full" 
            disabled={isSubmitting}
          >
            {isSubmitting ? (
              <>
                <Loader className="mr-2 h-4 w-4 animate-spin" />
                Soumission en cours...
              </>
            ) : (
              <>
                <FileUp className="mr-2 h-4 w-4" />
                Soumettre
              </>
            )}
          </Button>
        </form>
      </CardContent>
    </Card>
  )
}